  /*background: linear-gradient(
      90deg,
      transparentize($color-background, 0.5),
      transparentize($color-background, 1)
    );*/
  position: absolute;
  top: 0;
  left: 0;
}

.media-shelf-container:after {
  content: "";
  height: 100%;
  width: 32px;
  /*background: linear-gradient(
      270deg,
      transparentize($color-background, 0.5),
      transparentize($color-background, 1)
    );*/
  position: absolute;
  top: 0;
  right: 0;
}

